import * as React from "react";

import "./ProjectInfo.scss";

export class ProjectInfo extends React.PureComponent {

    render() {
        return <div id="projectInfo">
            <div id="showIcon"
                onClick={this.showContent.bind(this)}
                className="infoIcon"
                style={{ color: 'white' }}>
                <i className="fas fa-info-circle"></i>
            </div>
            <div id="hideIcon"
                onClick={this.hideContent.bind(this)}
                className="infoIcon"
                style={{ display: 'none' }}>
                <i className="fas fa-times"></i>
            </div>
            <div className="container"
                style={{ display: 'none' }}>
                <div className="content">
                    <h3>
                        <br />
                        3sth.net
                    </h3>
                    <br />
                    2006()()<br />
                    <br />
                    <br />
                    <br />
                    ()()()<br />
                    <br />
                    <br />
                    2018/10/16-11/11<br />
                     205<br /><br />
                    <a target="_blank" href="https://aigaerithm.3sth.net/"></a>

                </div>
            </div>
        </div>;
    };



    private showContent() {
        $('#projectInfo .container').fadeIn();
        $('#showIcon').hide();
        $('#hideIcon').show();
    };
    private hideContent() {
        $('#projectInfo .container').fadeOut();
        $('#showIcon').show();
        $('#hideIcon').hide();
    };
};